import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import "./Navbar.css";

const Navbar = () => {
    const [authenticated, setAuthenticated] = useState(false);

    useEffect(() => {
        localStorage.getItem("token") === null
            ? setAuthenticated(false)
            : setAuthenticated(true);
    }, []);

    const history = useHistory();

    return (
        <nav className="navbar-container">
            <div className="navbar-left-portion" onClick={()=>{history.push("/")}}>
                <img
                    alt="logo"
                    src="https://play-lh.googleusercontent.com/SpoozAfBft3HEAvsEV8xHTScJnIzLLWBkt-vb2hONjrsYWn8D7bsm4vKjzp_3-JI4N_K"
                />
                <p>Edifice</p>
            </div>
            <div className="navbar-right-portion">
                {authenticated ? (
                    <p
                        onClick={() => {
                            localStorage.removeItem("token");
                            history.push("/");
                            window.location.reload();
                        }}
                    >
                        LOGOUT
                    </p>
                ) : (
                    <p onClick={() => history.push("/login")}>LOGIN</p>
                )}
            </div>
        </nav>
    );
};

export default Navbar;
