import React, { useEffect, useState } from "react";
import "./Home.css";
import Navbar from "../../Components/Navbar/Navbar";
import Card from "../../Components/Card/Card";
import axios from "axios";
import { serverUrl } from "../../values/server";
import ReactLoading from "react-loading";
import { useHistory } from "react-router-dom";

const Home = () => {
    window.document.title = "Home | Edifice";

    const [loading, setLoading] = useState([]);
    const [myCourses, setMyCourses] = useState([]);

    const [allCourses, setAllCourses] = useState([]);

    const [courseTags, setCourseTags] = useState([]);
    const [courseProviders, setCourseProviders] = useState([]);

    const history = useHistory();

    useEffect(() => {
        if (localStorage.getItem("token") !== null) {
            getMyCourses();
            getAllCourses();
            getCourseProviders();
        } else {
            history.replace("/login");
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [history]);

    const getMyCourses = () => {
        setLoading(true);

        const data = JSON.stringify({});

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourseEnroll-list`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            setMyCourses(response.data);
        });
    };

    const getAllCourses = () => {
        setLoading(true);

        const data = JSON.stringify({});

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourse-list-v2`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            setAllCourses(response.data);
            getTags(response.data);
        });
    };

    const getTags = (allCourses) => {
        let tags = [];
        for (const course of allCourses) {
            for (const tag of course.tags) {
                if (!tags.includes(tag)) {
                    tags.push(tag);
                }
            }
        }

        setCourseTags(tags);
    };

    const getCourseProviders = () => {
        setLoading(true);

        const data = JSON.stringify({});

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourseProvider-list`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            console.log(response.data);
            setCourseProviders(response.data);
            setLoading(false);
        });
    };

    return (
        <>
            <Navbar />
            <section>
                <div className="homepage-header top-margin">
                    <p>My Courses</p>
                    <div></div>
                </div>
                <div className="homepage-course-cards-list">
                    {loading ? (
                        <div className="loading-spin-container">
                            <ReactLoading type={"spinningBubbles"} color={"#00bc4e"} />
                        </div>
                    ) : myCourses.length === 0 ? (
                        <div className="loading-spin-container">
                            Enrolled Courses will appear here
                        </div>
                    ) : (
                        myCourses.map((course) => (
                            <Card
                                courseName={course.course.name}
                                courseType=""
                                thumbnail={
                                    serverUrl +
                                    "/files/pics/" +
                                    course.course.logo
                                }
                                courseId={course.course._id}
                                toEnroll={false}
                            />
                        ))
                    )}
                </div>
            </section>
            {loading ? (
                ""
            ) : (
                <section>
                    <div className="homepage-header">
                        <p>Course Providers</p>
                        <div></div>
                    </div>
                    <div className="homepage-course-provider-list">
                        {courseProviders.map((provider) => (
                            <button
                                onClick={() => {
                                    history.push(`/provider/${provider._id}`);
                                }}
                            >
                                {provider.name}
                            </button>
                        ))}
                    </div>
                </section>
            )}
            {loading
                ? ""
                : courseTags.map((tag) => {
                      return (
                          <section>
                              <div className="homepage-header">
                                  <p>{tag}</p>
                                  <div></div>
                              </div>
                              <div className="homepage-course-cards-list">
                                  {allCourses.map((course) =>
                                      !course.tags.includes(tag) ? (
                                          ""
                                      ) : (
                                          <Card
                                              courseName={course.name}
                                              courseType={
                                                  course.paid
                                                      ? "PREMIUM"
                                                      : "FREE"
                                              }
                                              thumbnail={
                                                  serverUrl +
                                                  "/files/pics/" +
                                                  course.logo
                                              }
                                              courseId={course._id}
                                              toEnroll={true}
                                          />
                                      )
                                  )}
                              </div>
                          </section>
                      );
                  })}
        </>
    );
};

export default Home;
