import React, { useEffect, useState } from "react";
import axios from "axios";
import { useParams } from "react-router";
import { serverUrl } from "../../values/server";
import "./Course.css";
import YouTube from "react-youtube";
import Navbar from "../../Components/Navbar/Navbar";
import { useHistory } from "react-router-dom";
import ReactLoading from "react-loading";

const Course = ({ courseName }) => {
    window.document.title = "Loading Lessons.. | Edifice";
    const [lessons, setLessons] = useState([]);
    const [currentLessonId, setCurrentLessonId] = useState("");
    const [currentVideo, setCurrentVideo] = useState("");
    const [currentLessonName, setCurrentLessonName] = useState("");
    const [currentBody, setCurrentBody] = useState("");

    const [loading, setLoading] = useState(false);

    const { courseId } = useParams();
    const history = useHistory();

    if (!localStorage.getItem("token")) {
        history.push("/");
    }

    useEffect(() => {
        getJoinStatus();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const fetchLessons = () => {
        console.log("loading lessons....");
        const data = JSON.stringify({ course: courseId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsLesson-list`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            response.data.every((data) => {
                if (!data.isExam) {
                    setCurrentVideo(youtube_parser(data.ytLink));
                    setCurrentLessonName(data.name);
                    setCurrentBody(data.body);
                    setCurrentLessonId(data._id);
                    window.document.title = data.name + " - Edifice";
                    return false;
                }
                return true;
            });

            response.data.forEach((data) => {
                setLessons((arr) => [
                    ...arr,
                    {
                        id: data._id,
                        name: data.name,
                        ytLink: data.ytLink,
                        isExam: data.isExam,
                        body: data.body,
                    },
                ]);
            });
            setLoading(false);
        });
    };

    const getJoinStatus = () => {
        setLoading(true);
        const data = JSON.stringify({ course: courseId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourseEnroll-status`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            console.log(response.data);

            if (response.data.status === "Enrolled") {
                fetchLessons();
            } else {
                history.replace("/");
            }
        });
    };

    async function markAttendance(lessonId) {
        const data = JSON.stringify({ lesson: lessonId });
        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsAttendance`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        await axios(config);
    }

    const youtube_parser = (url) => {
        var regExp =
            /^https?:\/\/(?:www\.youtube(?:-nocookie)?\.com\/|m\.youtube\.com\/|youtube\.com\/)?(?:ytscreeningroom\?vi?=|youtu\.be\/|vi?\/|user\/.+\/u\/\w{1,2}\/|embed\/|watch\?(?:.*&)?vi?=|&vi?=|\?(?:.*&)?vi?=)([^#&?\n/<>"']*)/i;
        var match = url.match(regExp);
        return match && match[1].length === 11 ? match[1] : false;
    };

    return (
        <>
            <Navbar />
            <div className="course-page-container top-margin">
                {loading ? (
                    <div className="loading-spin-container">
                        <ReactLoading type={"spinningBubbles"} color={"#00bc4e"} />
                    </div>
                ) : (
                    <>
                        <div className="course-page-video-container">
                            <YouTube
                                className="yt-player"
                                videoId={currentVideo}
                                opts={{
                                    width: "100%",
                                    height: "100%",
                                    playerVars: {
                                        modestbranding: 1,
                                        rel: 0,
                                        showinfo: 0,
                                    },
                                }}
                            />
                            <p className="ytTitle">{currentLessonName}</p>
                            <p className="ytBody">{currentBody}</p>
                        </div>
                        <div className="course-name-lessons-container">
                            {lessons.map((lesson) =>
                                !lesson.isExam ? (
                                    <div
                                        key={lesson.id}
                                        className={
                                            currentLessonId === lesson.id
                                                ? "active-lesson-card"
                                                : ""
                                        }
                                        onClick={() => {
                                            setCurrentVideo(
                                                youtube_parser(lesson.ytLink)
                                            );
                                            setCurrentLessonName(lesson.name);
                                            setCurrentBody(lesson.body);
                                            setCurrentLessonId(lesson.id);
                                            markAttendance(lesson.id);
                                            window.document.title =
                                                lesson.name + " | Edifice";
                                            window.scrollTo(0, 0);
                                        }}
                                    >
                                        <p>{lesson.name}</p>
                                        <p>{lesson.body}</p>
                                    </div>
                                ) : null
                            )}
                        </div>
                    </>
                )}
            </div>
        </>
    );
};

export default Course;
