import axios from "axios";
import React, { useState } from "react";
import { serverUrl } from "../../values/server";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import ReactLoading from "react-loading";

const ForgotPassword = () => {
    window.document.title = "Change Password | Edifice";

    const [email, setEmail] = useState("");
    const [loading, setLoading] = useState(false);

    const clearFields = () => {
        setEmail("");
    };

    const showToast = (msg) => {
        toast(msg, {
            style: {
                background: "var(--errorRed)",
                color: "white",
                textAlign: "center",
            },
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
        });
    };

    const sendLink = () => {
        setLoading(true);
        if (!email) {
            setLoading(false);
            showToast("Enter email");
            clearFields();
        } else {
            const data = JSON.stringify({
                email: email,
            });
            const config = {
                method: "post",
                url: `${serverUrl}/users/reset-pass`,
                headers: {
                    "Content-Type": "application/json",
                },
                data: data,
            };

            axios(config)
                .then((response) => {
                    setLoading(false);
                    clearFields();
                    showToast(
                        response.data.msg === "PENDING"
                            ? "Already sent!"
                            : "Password reset link sent! Please check your inbox."
                    );
                    console.log(response.data);
                })
                .catch((err) => {
                    if (err.message === "Request failed with status code 500") {
                        showToast("Invalid email or password");
                    }
                    setLoading(false);
                    clearFields();
                });
        }
    };

    return (
        <>
            <div className="login-wrapper">
                <div className="login-container" style={{ height: "auto" }}>
                    <div className="login-header-row">
                        <img
                            alt="logo"
                            src="https://play-lh.googleusercontent.com/SpoozAfBft3HEAvsEV8xHTScJnIzLLWBkt-vb2hONjrsYWn8D7bsm4vKjzp_3-JI4N_K"
                        />
                        <p>EDIFICE</p>
                    </div>
                    <div className="login-header-desc">
                        <p>Enter your email for password reset link.</p>
                    </div>
                    <div className="login-input-container">
                        <input
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="Email"
                            type="email"
                        />
                    </div>
                    <div className="login-footer-row">
                        <div onClick={sendLink} className="login-button">
                            {!loading ? (
                                `Send Link`
                            ) : (
                                <ReactLoading
                                    type={"spinningBubbles"}
                                    color={"white"}
                                    width={"10%"}
                                    height={"60%"}
                                />
                            )}
                        </div>
                    </div>
                </div>
                <ToastContainer />
            </div>
        </>
    );
};

export default ForgotPassword;
