import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import { serverUrl } from "../../values/server";
import "./EnrollCourse.css";
import ReactLoading from "react-loading";
import Navbar from "../../Components/Navbar/Navbar";
import "../../Components/Card/Card.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function EnrollCourse() {
    const { courseId } = useParams();
    const [loading, setLoading] = useState(false);
    const [courseDetails, setCourseDetails] = useState({});

    const [isEnrolled, setIsEnrolled] = useState(false);

    const history = useHistory();

    useEffect(() => {
        window.scrollTo(0, 0);
        const script = document.createElement("script");
        script.src = "https://checkout.razorpay.com/v1/checkout.js";
        script.async = true;
        document.body.appendChild(script);
        getCourseDetails();
        getJoinStatus();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const showToast = (msg) => {
        toast(msg, {
            style: {
                background: "var(--errorRed)",
                color: "white",
                textAlign: "center",
            },
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
        });
    };

    const getCourseDetails = () => {
        setLoading(true);

        const data = JSON.stringify({ _id: courseId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourse-by-id`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            setCourseDetails(response.data);
            console.log(response.data);
            setLoading(false);
        });
    };

    const getJoinStatus = () => {
        setLoading(true);

        const data = JSON.stringify({ course: courseId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourseEnroll-status`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            if (response.data.status === "Enrolled") {
                setIsEnrolled(true);
            } else {
                setIsEnrolled(false);
            }
            setLoading(false);
        });
    };

    const enrollCourse = () => {
        setLoading(true);

        const data = JSON.stringify({ course: courseId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/add-lmsCourseEnroll`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            window.location.reload();
            setLoading(false);
        });
    };

    const pay = () => {
        setLoading(true);
        const data = JSON.stringify({
            amount: courseDetails.fee * 100,
            receipt: "",
        });
        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-orderid-for-razorpay`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config)
            .then((response) => {
                setLoading(false);
                var options = {
                    key: courseDetails.provider.razorpayKeyId,
                    amount: courseDetails.fee * 100,
                    name: courseDetails.name,
                    handler: function (response1) {
                        if (response1.razorpay_order_id !== undefined) {
                            enrollCourse();
                        }
                    },
                    description: courseDetails.category.name,
                    order_id: response.data.order_id,
                    theme: {
                        color: "#32B44A",
                    },
                };

                var rzp1 = new window.Razorpay(options);
                rzp1.open();
            })
            .catch((err) => {
                setLoading(false);
                showToast("Something went wrong");
            });
    };

    return (
        <>
            <Navbar />
            <div className="main">
                <div className="card">
                    {loading ? (
                        <div className="loading-spin-container">
                            <ReactLoading type={"spinningBubbles"} color={"#00bc4e"} />
                        </div>
                    ) : (
                        <>
                            <div className="title-row">
                                <div style={{ display: "flex" }}>
                                    <div className="thumbnail">
                                        <img
                                            src={
                                                serverUrl +
                                                "/files/pics/" +
                                                courseDetails.logo
                                            }
                                            alt=""
                                        ></img>
                                    </div>
                                    <div className="column">
                                        <p>
                                            {courseDetails.name} (
                                            {courseDetails.code})
                                        </p>
                                    </div>
                                </div>
                                <button
                                    onClick={() => {
                                        if (isEnrolled) {
                                            history.push(`/course/${courseId}`);
                                        } else if (courseDetails.paid) {
                                            pay();
                                        } else {
                                            enrollCourse();
                                        }
                                    }}
                                >
                                    {isEnrolled
                                        ? "Go to Course"
                                        : courseDetails.paid
                                        ? "Pay & Enroll"
                                        : "Enroll"}
                                </button>
                            </div>
                            <p>{courseDetails.description}</p>
                        </>
                    )}
                </div>
                <div
                    className="top"
                    style={{
                        backgroundImage: `url(${
                            serverUrl + "/files/pics/" + courseDetails.logo
                        })`,
                    }}
                >
                    <div className="gradient"></div>
                </div>
                <div className="bottom"></div>
            </div>
            <ToastContainer />
        </>
    );
}

export default EnrollCourse;
