import axios from "axios";
import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { serverUrl } from "../../values/server";
import "./Login.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import ReactLoading from "react-loading";

const Login = () => {
    window.document.title = "Login | Edifice";

    const [userName, setUserName] = useState("");
    const [password, setPassword] = useState("");
    const [loading, setLoading] = useState(false);

    const history = useHistory();

    const clearFields = () => {
        setUserName("");
        setPassword("");
    };

    const showToast = (msg) => {
        toast(msg, {
            style: {
                background: "var(--errorRed)",
                color: "white",
                textAlign: "center",
            },
            position: "bottom-center",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
        });
    };

    const onLogin = () => {
        setLoading(true);
        if (!userName || !password) {
            setLoading(false);
            showToast("Email and Password must not be empty");
            clearFields();
        } else {
            const data = JSON.stringify({
                org_id: "5f143f5d93c1f321f663aaf6",
                username: userName,
                password: password,
            });
            const config = {
                method: "post",
                url: `${serverUrl}/users/authenticate`,
                headers: {
                    "Content-Type": "application/json",
                },
                data: data,
            };

            axios(config)
                .then((response) => {
                    localStorage.setItem("token", response.data.user.token);
                    history.push("/");
                    setLoading(false);
                    clearFields();
                    console.log(response.data.user.token);
                    window.location.reload();
                })
                .catch((err) => {
                    if (err.message === "Request failed with status code 500") {
                        showToast("Invalid email or password");
                    }
                    setLoading(false);
                    clearFields();
                });
        }
    };

    return (
        <>
            <div className="login-wrapper">
                <div className="login-container">
                    <div className="login-header-row">
                        <img
                            alt="logo"
                            src="https://play-lh.googleusercontent.com/SpoozAfBft3HEAvsEV8xHTScJnIzLLWBkt-vb2hONjrsYWn8D7bsm4vKjzp_3-JI4N_K"
                        />
                        <p>EDIFICE</p>
                    </div>
                    <div className="login-header-desc">
                        <p>Log in to Edifice with email and password.</p>
                    </div>
                    <div className="login-input-container">
                        <input
                            value={userName}
                            onChange={(e) => setUserName(e.target.value)}
                            placeholder="Email"
                            type="email"
                        />
                        <input
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder="Password"
                            type="password"
                        />
                    </div>
                    <div className="login-forgot-password-container" onClick={()=>history.push("/forgot-password")}>
                        <p>Forgot Password</p>
                    </div>
                    <div className="login-footer-row">
                        <div className="login-text-button">
                            <p>Not Registered?</p>
                            <p>Register Now</p>
                        </div>
                        <div onClick={onLogin} className="login-button">
                            {!loading ? (
                                `LOG IN`
                            ) : (
                                <ReactLoading
                                    type={"spinningBubbles"}
                                    color={"white"}
                                    width={"10%"}
                                    height={"60%"}
                                />
                            )}
                        </div>
                    </div>
                </div>
                <ToastContainer />
            </div>
        </>
    );
};

export default Login;
