import "./App.css";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./Pages/Home/Home";
import Login from "./Pages/Login/Login";
import Course from "./Pages/Course/Course";
import ForgotPassword from "./Pages/ForgotPassword/ForgotPassword";
import EnrollCourse from "./Pages/EnrollCourse/EnrollCourse";
import CourseProvider from "./Pages/CourseProvider/CourseProvider";
import Jamia from "./Pages/CourseProvider/Jamia";

const App = () => {
    return (
        <>
            <Router>
                <Switch>
                    <Route exact path="/" component={localStorage.getItem("token") !== null ? Home : Login} />
                    <Route exact path="/login" component={Login} />
                    <Route exact path="/forgot-password" component={ForgotPassword} />
                    <Route exact path="/course/:courseId"  component={localStorage.getItem("token") !== null ? Course : Login} />
                    <Route exact path="/course/enroll/:courseId" component={localStorage.getItem("token") !== null ? EnrollCourse : Login} />
                    <Route exact path="/provider/:providerId" component={localStorage.getItem("token") !== null ? CourseProvider : Login} />
                    <Route exact path="/jamia" component={localStorage.getItem("token") !== null ? Jamia : Login} />
                </Switch>
            </Router>
        </>
    );
};

export default App;
