import React, { useEffect, useState } from "react";
import Navbar from "../../Components/Navbar/Navbar";
import "./CourseProvider.css";
import { serverUrl } from "../../values/server";
import axios from "axios";
import Card from "../../Components/Card/Card";
import { useParams } from "react-router";
import ReactLoading from "react-loading";

function CourseProvider() {
    const [loading, setLoading] = useState(false);
    const [courses, setCourses] = useState([]);
    const [providerDetails, setProviderDetails] = useState({});

    const { providerId } = useParams();

    const getProviderDetails = () => {
        setLoading(true);

        const data = JSON.stringify({ _id: providerId });

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourseProvider-by-id`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            setProviderDetails(response.data);
        });
    };

    const getCourses = () => {
        setLoading(true);

        const data = JSON.stringify({});

        const config = {
            method: "post",
            url: `${serverUrl}/lms/get-lmsCourse-list-v2`,
            headers: {
                "Content-Type": "application/json",
                Authorization: `JWT ${localStorage.getItem("token")}`,
            },
            data: data,
        };

        axios(config).then((response) => {
            let coursesList = [];
            for (const course of response.data) {
                if (course.provider._id === providerId) {
                    coursesList.push(course);
                }
            }
            setCourses(coursesList);
            setLoading(false);
        });
    };

    useEffect(() => {
        getProviderDetails();
        getCourses();
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div>
            <Navbar />
            {loading ? (
                <div
                    className="loading-spin-container"
                    style={{ marginTop: "4rem" }}
                >
                    <ReactLoading type={"spinningBubbles"} color={"#00bc4e"} />
                </div>
            ) : (
                <section>
                    <div className="homepage-header top-margin">
                        <p>Courses from {providerDetails.name}</p>
                        <div></div>
                    </div>
                    <div className="homepage-course-cards-list">
                        {courses.length === 0 ? (
                            <div className="loading-spin-container">
                                No courses from {providerDetails.name}
                            </div>
                        ) : (
                            courses.map((course) => (
                                <Card
                                    courseName={course.name}
                                    courseType={
                                        course.paid ? "PREMIUM" : "FREE"
                                    }
                                    thumbnail={
                                        serverUrl + "/files/pics/" + course.logo
                                    }
                                    courseId={course._id}
                                    toEnroll={true}
                                />
                            ))
                        )}
                    </div>
                </section>
            )}
        </div>
    );
}

export default CourseProvider;
