import React from "react";
import { useHistory } from "react-router-dom";
import "./Card.css";

const Card = ({ courseName, courseType, thumbnail, courseId, toEnroll }) => {
    const history = useHistory();

    return (
        <div
            onClick={() => history.push(toEnroll ? `/course/enroll/${courseId}` : `/course/${courseId}`)}
            className="course-card"
        >
            <div>
                <div className="thumbnail">
                    <img src={thumbnail} alt=""></img>
                </div>
            </div>
            <p>{courseName}</p>
            <p
                style={
                    courseType.toUpperCase() === "PREMIUM"
                        ? { color: "orange" }
                        : courseType.toUpperCase() === "FREE"
                        ? { color: "var(--edificeGreen)" }
                        : { color: "grey" }
                }
            >
                {courseType.toUpperCase()}
            </p>
        </div>
        // <div className="homepage-course-card">
        //     <div>
        //         <div></div>
        //     </div>
        //     <p>Lorem ipsum sample Course</p>
        //     <p
        //         style={
        //             courseType.toUpperCase() === "PREMIUM"
        //                 ? { color: "orange" }
        //                 : courseType.toUpperCase() === "FREE"
        //                 ? { color: "green" }
        //                 : null
        //         }
        //     >
        //         {courseType.toUpperCase()}
        //     </p>
        // </div>
    );
};

export default Card;
